.login_container{
    width: 90%;
    max-width: 400px;
    background-color: white;
    padding: 20px;
    text-align: left;
    font-size: 20px;
}

.userName{
    font-size: 13px;
}

.input-group{
    position: relative;
    margin-bottom: 20px;
}

.input-group label{
    font-size: 14px;
    color: black;
}

.input-group input{
    width: 80%;
    padding: 10px;
    padding-right: 40px;
    font-size: 16px;
    border: 1px solid #ddd;
    border-radius: 5px;
    margin-top: 10px;
}

.nextPage button{
    width: 100%;
    height: 40px;
    border-radius: 5px;
    border: 1px solid #ddd;
    margin-top: 10px;
}   

.clear-btn:hidden{
    display: none;
}

.clear-btn{
    position: absolute;
    top:  35%;
    right: 10px;
    background: none;
    border: none;
    color: #888;
    font-size:16px;
    cursor: pointer;
    
}

.next-btn{
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    cursor: pointer;
    
}

.next-btn:disabled{
    background-color: #ddd;
    color: #888;
    cursor: not-allowed;
}

.next-btn:not(:disabled){
    background-color: #9a9af4;
    color:white;
    
}
.next-btn:not(:disabled):hover{
    background-color: #7b7bd4;
}