.login_container {
    width: 90%;
    max-width: 400px;
    background-color: white;
    padding: 20px;
    text-align: left;
    font-size: 20px;
}

.userName {
    font-size: 13px;
}

.input-group {
    position: relative;
    margin-bottom: 20px;
}

.input-group label {
    font-size: 14px;
    color: black;
}

.input-group input {
    width: 80%;
    padding: 10px;
    padding-right: 40px;
    font-size: 16px;
    border: 1px solid #ddd;
    border-radius: 5px;
    margin-top: 10px;
}

.nextPage button {
    width: 100%;
    height: 40px;
    border-radius: 5px;
    border: 1px solid #ddd;
    margin-top: 10px;
}

.next-btn {
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    cursor: pointer;

}

.next-btn:disabled {
    background-color: #ddd;
    color: #888;
    cursor: not-allowed;
}

.next-btn:not(:disabled) {
    background-color: #9a9af4;
    color: white;

}

.next-btn:not(:disabled):hover {
    background-color: #7b7bd4;
}

.gender-group {
    justify-content: space-around;
    display: flex;
    margin-top: 10px;
}

.gender-group label {
    display: flex;
    align-items: center;
}

.gender-group label input {
    display: none;
}

.checkbox-custom {
    width: 20px;
    height: 20px;
    display: inline-block;
    background-size: cover;
    margin-right: 10px;
    background-image: url('../../../public/images/checkmark-off.png');
}

input[type='radio']:checked+.checkbox-custom {
    background-image: url('../../../public/images/checkmark-on.png');
}